import React from "react";
import { MDBContainer, MDBRow, MDBCol, MDBBtn, MDBIcon } from "mdbreact";

const Footer = (props) => (
  <div className="footer">
    <MDBContainer fluid>
      <MDBRow>
        <MDBCol lg="3">
          <div className="footer_wrap">
            <div className="footer_logo">
              <img
                alt="logo"
                src={require("../../assets/img/logo.png")}
                width="200"
              />
            </div>
            <div className="footer_desc">
              <p>
                Sed ut perspiciatis unde omn iste natus error sit voluptatem.
              </p>
            </div>
            <div className="footer_head">
              <h2>Join Newsletters</h2>
            </div>
            <div className="newsletter_form">
              <div className="content_form">
                <input
                  type="email"
                  name="EMAIL"
                  placeholder="Email address"
                  required=""
                  className="form-control"
                />
                <div className="form_action">
                  <MDBBtn color="amber">
                    <MDBIcon far icon="paper-plane" />
                  </MDBBtn>
                </div>
              </div>
            </div>
          </div>
        </MDBCol>
        <MDBCol lg="2">
          <div className="footer_wrap">
            <div className="footer_head">
              <h2>Our Products</h2>
            </div>
            <div className="footer_links">
              <ul>
                <li>
                  <a href="#!">
                    <MDBIcon icon="chevron-right" /> Home{" "}
                  </a>
                </li>
                <li>
                  <a href="#!">
                    <MDBIcon icon="chevron-right" /> About Us
                  </a>
                </li>
                {/* <li>
                  <a href="#!">
                    <MDBIcon icon="chevron-right" /> Products
                  </a>
                </li> */}
                <li>
                  <a href="#!">
                    <MDBIcon icon="chevron-right" /> Poja Booking
                  </a>
                </li>
                <li>
                  <a href="#!">
                    <MDBIcon icon="chevron-right" /> Blog{" "}
                  </a>
                </li>
                <li>
                  <a href="#!">
                    <MDBIcon icon="chevron-right" /> Contact us
                  </a>
                </li>
              </ul>
            </div>
          </div>
        </MDBCol>
        <MDBCol lg="3">
          <div className="footer_wrap">
            <div className="footer_head">
              <h2>Address</h2>
            </div>
            <div className="footer_links">
              <iframe
                src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d14029.837290147692!2d77.28751278192712!3d28.465710769740696!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x390ce748c2a11017%3A0x350c7bca05dd147f!2sGreenfields%2C%20Sector%2042%2C%20Faridabad%2C%20Haryana%20121003!5e0!3m2!1sen!2sin!4v1645603243745!5m2!1sen!2sin"
                width="100%"
                height="250"
                style={{ border: 0 }}
                allowFullScreen=""
                loading="lazy"
                title="maps"
              ></iframe>
            </div>
          </div>
        </MDBCol>
        <MDBCol lg="4">
          <div className="footer_wrap">
            <div className="footer_head">
              <h2>Contact</h2>
            </div>
            <div className="footer_contact">
              <ul>
                <li>
                  <div className="icons">
                    <MDBIcon icon="phone-alt" />
                  </div>
                  <div className="box_content">
                    <h3>Phone Number</h3>
                    <p>
                      <a href="tel:+918385842313">+91-8385842313</a>
                    </p>
                  </div>
                </li>
                <li>
                  <div className="icons">
                    <MDBIcon far icon="envelope-open" />
                  </div>
                  <div className="box_content">
                    <h3>Email Address</h3>
                    <p>
                      <a href="mailto:info@pujyapanditg.com">
                        info@pujyapanditg.com
                      </a>
                    </p>
                  </div>
                </li>
                <li>
                  <div className="icons">
                    <MDBIcon icon="map-marker-alt" />
                  </div>
                  <div className="box_content">
                    <h3>Address</h3>
                    <p>Head office Jaipur , 2nd Branch Bhadra , India</p>
                  </div>
                </li>
              </ul>
            </div>
          </div>
        </MDBCol>
        <MDBCol lg="12">
          <div className="copyright">
            <p>Copyright  2022 pujyapanditg. All Right Reserved.</p>
          </div>
        </MDBCol>
      </MDBRow>
    </MDBContainer>
  </div>
);
export default Footer;
