import React from "react";
// Import Swiper React components
import { SwiperSlide } from "swiper/react";
import SliderWrapper from "../Slider";

const Banner = (props) => {
  return (
    <SliderWrapper slides={1} row={1} spacebetween={0} colors="#c74a2d">
      <SwiperSlide>
        <img
          src="https://demos.codexcoder.com/peace/wp-content/uploads/revslider/Hindu_Festival_BG_Image_03-1.jpg"
          alt="banner"
          className="img-fluid"
        />
      </SwiperSlide>
      <SwiperSlide>
        <img
          src="https://demos.codexcoder.com/peace/wp-content/uploads/revslider/Hindu_Festival_BG_Image_03-1.jpg"
          alt="banner"
          className="img-fluid"
        />
      </SwiperSlide>
    </SliderWrapper>
  );
};
export default Banner;
