import React, { Component } from "react";
import {
  MDBNavbar,
  MDBNavbarBrand,
  MDBNavbarNav,
  MDBNavbarToggler,
  MDBCollapse,
  MDBNavItem,
  MDBNavLink,
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBIcon,
} from "mdbreact";
import { BrowserRouter as Router } from "react-router-dom";
import Logo from "../../assets/img/logo.png";

class Header extends Component {
  state = {
    collapseID: "",
  };

  toggleCollapse = (collapseID) => () =>
    this.setState((prevState) => ({
      collapseID: prevState.collapseID !== collapseID ? collapseID : "",
    }));

  closeCollapse = (collID) => () => {
    const { collapseID } = this.state;
    window.scrollTo(0, 0);
    collapseID === collID && this.setState({ collapseID: "" });
  };

  render() {
    const { collapseID } = this.state;
    return (
      <Router>
        <div className="Header">
          <div className="top_bar">
            <MDBContainer fluid>
              <MDBRow>
                <MDBCol lg="6">
                  <ul className="top_menu">
                    <li>
                      <a href="tel:+918385842313">
                        <MDBIcon icon="phone-alt" /> +91-8385842313
                      </a>
                    </li>
                    <li>
                      <a href="mailto:info@pujyapanditg.com">
                        <MDBIcon far icon="envelope-open" />{" "}
                        info@pujyapanditg.com
                      </a>
                    </li>
                  </ul>
                </MDBCol>
                <MDBCol lg="6"></MDBCol>
              </MDBRow>
            </MDBContainer>
          </div>
          <MDBNavbar color="white" light expand="md" scrolling>
            <MDBNavbarBrand href="/" className="py-0 font-weight-bold">
              <img
                src={Logo}
                style={{
                  width: "200px",
                  position: "absolute",
                  top: "5px",
                  zIndex: "9",
                }}
                alt="Logo"
              />
            </MDBNavbarBrand>
            <MDBNavbarToggler
              onClick={this.toggleCollapse("mainNavbarCollapse")}
            />
            <MDBCollapse id="mainNavbarCollapse" isOpen={collapseID} navbar>
              <MDBNavbarNav right>
                <MDBNavItem>
                  <MDBNavLink exact to="/">
                    <strong>Home</strong>
                  </MDBNavLink>
                </MDBNavItem>
                <MDBNavItem>
                  <MDBNavLink to="/css">
                    <strong>About</strong>
                  </MDBNavLink>
                </MDBNavItem>
                {/* <MDBNavItem>
                  <MDBNavLink to="/components">
                    <strong>Products</strong>
                  </MDBNavLink>
                </MDBNavItem> */}
                <MDBNavItem>
                  <MDBNavLink to="/advanced">
                    <strong>Pooja Booking</strong>
                  </MDBNavLink>
                </MDBNavItem>
                <MDBNavItem>
                  <MDBNavLink
                    to="/advanced"
                    className="btn btn-outline-amber btn-rounded px-4"
                  >
                    <strong>register</strong>
                  </MDBNavLink>
                </MDBNavItem>
                <MDBNavItem>
                  <MDBNavLink
                    to="/advanced"
                    className="btn btn-amber btn-rounded px-4 text-white"
                  >
                    <strong>Login</strong>
                  </MDBNavLink>
                </MDBNavItem>
              </MDBNavbarNav>
            </MDBCollapse>
          </MDBNavbar>
        </div>
      </Router>
    );
  }
}

export default Header;
